.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Layout für die gesamte App */
.container {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

/* Der Hauptinhalt (z.B. die Karte), der den verbleibenden Platz ausfüllt */
.content {
  flex: 1;
  position: relative;
  overflow: hidden; /* Verhindert, dass Inhalte aus dem Container fließen */
  margin: 65px 0;
}

